@import '_variables';
@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --element: hsl(209, 23%, 22%);
    --bg: hsl(207, 26%, 17%);
    --text: hsl(0, 0%, 100%);
    --reduce: hsl(0, 0%, 70%);
    --moon: url('../assets/moon-solid.svg');
    --filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(171deg) brightness(107%) contrast(101%);
    --shadow: hsla(0, 0%, 0%, 0.3);
}

/* Start styling the attribution */
.attribution { 
    position: fixed;
    bottom: 0rem;
    left: 50%;
    transform: translateX(-50%);
    font-size: 0.7rem; 
    line-height: 1.6; 
    text-align: center; 
    color:hsl(220, 15%, 55%);
    width: 100%;
}
.attribution a { 
    color: var(--Soft-blue);
}
.attribution a:hover{
    color: var(--Cyan);
    transition: color .3s;
}

/* Screen reader class to hide the content visually */
.sr-only {
    position: absolute !important;
    margin: -1px !important;
    -webkit-clip-path: inset(50%) !important;
            clip-path: inset(50%) !important;
    border: 0 !important;
    padding: 0 !important;
    width: 1px !important;
    height: 1px !important;
    overflow: hidden !important;
    white-space: nowrap !important;
}

/*General and page initial setup*/
html{
    font-size: 100%;
    font-family: $font;
    scroll-behavior: smooth;
    
}

img{
    max-width: 100%;
    height: auto;
}

main{
    width: 100vw;
    min-height: 100vh;
    background-color: var(--bg);
    color: var(--text);

}

.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 1rem;
    background-color: var(--element);
    box-shadow: 0px 0px 10px 0px var(--shadow);

   

    h2{
        font-weight: 600;
        font-size: $f-detail;
        letter-spacing: 1.5px;
    }

    
    .toggle{
        display: flex;
        align-items: flex-end;
        position: relative;
        gap: 0.5rem;
        

        #theme{
            width: 20px;
            height: 20px;
            
            cursor: pointer;
            opacity: 0;
        }
        svg{
            pointer-events: none;
            cursor: pointer;
            width: 20px;
            height: 20px;
            top: 0px;
            left: 0;
            position: absolute;
    
            transform-origin: center;
            transform: rotate(-30deg);
        }

        label{
            font-size: $f-home;
            letter-spacing: 1.5px;
            font-weight: 300;
            cursor: pointer;
           
        }
    }

    @media screen and (min-width: 1024px){
        padding: 1.5rem 5rem;

        h2{
            font-size: 22px;
            letter-spacing: 1px;

        }

        .toggle{
            align-items: center;
            gap: 0.5rem;

            svg{
                width: 18px;
                height: 18px;
                top: 2px;
            }

            label{
                font-size: 16px;
                letter-spacing: 0.8px;

                
            }
        }
    }
}

.containeur{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    .search{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        
        width: 100%;
        padding: 1.5rem 1rem 2rem;

        input[type="search"]::-webkit-search-decoration,
        input[type="search"]::-webkit-search-cancel-button,
        input[type="search"]::-webkit-search-results-button,
        input[type="search"]::-webkit-search-results-decoration {
            display: none;
        }

        input[type=search]{
            cursor: help;
            border: none;
            
            width: 100%;
            
            padding: 1.5rem 1.5rem 1.4rem 20%;
            display: flex;
            align-items: flex-end;
            background-color: var(--element);
            border-radius: 6px;

            color: var(--text);
            font-size: $f-detail;
            box-shadow: 0px 0px 10px 0px var(--shadow);

            &::placeholder{
                color: var(--reduce);
                font-size: $f-detail;
                
                
                
            }

           
        }

        label{
          
         
                width: 20px;
                height: 20px;
                top:  calc(1.5rem + 1rem + 5px);
                left: 12%;
                position: absolute;
                

                
                filter: var(--filter);
                background-image: url('../assets/magnifying-glass-solid.svg');
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                transform-origin: center;
            
        }

        select::-ms-expand { display: none; }

        select{
            -moz-appearance:none; /* Firefox */
            -webkit-appearance:none; /* Safari and Chrome */
            appearance:none;
            position: relative;
            border: none;
            width: 58%;
            box-shadow: 0px 0px 10px 0px var(--shadow);
            cursor: pointer;
            border-radius: 6px;
            padding: 1.5rem 1rem 1.4rem;
            background-color: var(--element);
            color: var(--reduce);
            font-weight: 300;
            font-size: $f-detail;
            margin-top: 2rem;
            
           
            
            option{
                margin-top: 2rem;
                width: 100%;
            }

        }

        .arrow-select{
            width: 16px;
            height: 16px;
            position: absolute;
            bottom: calc(2rem + 1.4rem);
            left: 45%;
            filter: var(--filter);
            background-image: url('../assets/chevron-down-solid.svg');
        }

        
    }
    @media screen and (min-width: 1024px) {
        

        .search{
            flex-direction: row;
            padding: 3rem 5rem;
            justify-content: space-between;


            input[type=search]{
                width: 30rem;
                padding: 1.5rem 1rem 1.25rem 4.5rem;
                font-size: $f-home;

                &::placeholder{
                    font-size: $f-home;
                }
            }

            label{
                width: 18px;
                height: 18px;
                top:  calc(3rem + 1.5rem - 2px);
                left: calc(5rem + 2.25rem - 5px);
                position: absolute;
            }

            select{
                width: 12.5rem;
                padding: 1.5rem 1.5rem 1.25rem;
                margin-top: 0;
                font-size: $f-home;
            }

            .arrow-select{
                width: 12px;
                height: 12px;
                position: absolute;
                bottom: calc(3rem + 1.5rem);
                left: unset;
                right: calc(5rem + 1.25rem);
                filter: var(--filter);
                background-image: url('../assets/chevron-down-solid.svg');
            }
        }
    }
}

.card-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0 3rem 3rem;
    row-gap: 3rem;

    @media screen and (min-width: 740px){
        justify-content: space-between;
        padding: 0 5rem 3rem;
        row-gap: 5rem;
    }
}

.card{
    display: flex;
    flex-direction: column;
    background-color: var(--element);
    width: 18rem;
    border-radius: 6px;
    overflow: hidden;
    cursor: pointer;
    box-shadow: 0px 0px 10px 0px var(--shadow);

    img{
        height: 160px;
    }

    .text-card{
        padding: 0rem 1rem 0rem 1.5rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-height: 11rem;

        h3{
            font-size: 18px;
            font-weight: 600;
            padding-bottom: 1rem;
        }

        p{
            font-size: $f-home;
            color: var(--text);
            font-weight: 600;
            padding-bottom: 0.4rem;

            span{
                font-weight: 300;
                color: var(--reduce);
            }
        }
    }

    @media screen and (min-width: 1024px) {
        width: 16.5rem;
    }
}

.detail{

    display: flex;
    flex-direction: column;
    align-items:flex-start;
    width: 100%;
    padding: 2rem 2rem 3rem;

    .back{
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: var(--element);
        width: 6.5rem;
        height: 2rem;
        gap: 0.2rem;
        border-radius: 4px;
        margin-bottom: 3rem;
        box-shadow: 0px 0px 10px 0px var(--shadow);

        svg{
            width: 20px;
            height: 16px;
            fill: var(--reduce);
        }

        p{
            font-size: $f-home;
            color: var(--reduce);
            letter-spacing: 1px;
        }
    }

    .info{
        display: flex;
        flex-direction: column;

        img{
            margin-bottom: 2rem;
        }

        .text-info{
            display: flex;
            flex-direction: column;

            h3{
                font-size: 26px;
                padding-bottom: 1rem;
            }


            .grid{
                display: flex;
                flex-direction: column;
                gap: 2rem;
                padding-bottom: 2rem;
                
                >div{
                    display: flex;
                    flex-direction: column;
                    gap: 0.7rem;

                    p{
                        font-weight: 600;
                        color: var(--reduce);
                        font-size: $f-detail;

                        span{
                            font-weight: 300;
                            color: var(--reduce);
                        }
                    }
                }
            }

            .borders{
                display: flex;
                flex-direction: column;
                gap: 0.5rem;
                

                p{
                    font-weight: 600;
                    font-size: 18px;
                }

                div{
                    display: flex;
                    flex-wrap: wrap;
                    width: 100%;
                   
                    justify-content: flex-start;
                    
                    gap: 0.5rem;

                    span{
                        cursor: pointer;
                        background-color: var(--element);
                        color: var(--reduce);
                        font-size: $f-home;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 4px;
                        padding: 0 1rem;
                        
                        min-width: 6rem;
                        min-height: 2rem;
                        box-shadow: 0px 0px 10px 0px var(--shadow);

                        
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 1024px){

    .containeur{
        min-height: 80vh;
        display: flex;
        align-items: center;
        justify-content: flex-start;
    }
    .detail{
        padding: 8vh 5rem 0;
        
        gap: 4vh;
        
        
        justify-content: space-between;

        .back{
            cursor: pointer;
            width: 8.5rem;
            height: 2.5rem;
            gap: 0.5rem;

            svg{
                height: 18px;
                margin-left: -15px;
            }
        }

        .info{
            flex-direction: row;
            justify-content: space-between;
            width: 100%;

            img{
                margin-bottom: 0;
                width: 35rem;
                height: 365px;
                box-shadow: 0px 0px 40px 0px var(--shadow);
            }

            .text-info{
                width: 45%;
                justify-content: center;

                h3{
                    font-size: 32px;
                    padding-bottom: 2rem;
                }

                .grid{
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    gap: 3.5rem;
                    padding-bottom: 4.5rem;

                    >div{
                        gap: 0.6rem;
                    }
                    
                }

                .borders{
                    flex-direction: row;
                    align-items: center;
                    gap: 1rem;
                    margin-bottom: 2rem;
    
                    p{
                        font-size: $f-detail;
                        flex-shrink: 0;
                    }
    
                    div{
                        width: unset;
                        gap: 0.5rem;
                        justify-content: start;

                        span{
                            height: 1.8rem;
                        }
                    }
                }
            }

            
        }
    }
}